import React from "react"
import Video from "../video/video";

const Thumbnail = ({projekt}) => {
    console.log(projekt);
    try {
        require(`./../../projekt/${projekt}/${projekt}.mp4`);
        return <Video project={projekt}/>;
    } catch (err) {
        return <img alt="blog thumbnail" src={require(`./../../projekt/${projekt}/${projekt}.png`)}/>;
    }
};


export default Thumbnail;
