import React from "react"

import Layout from "../elements/layout"
import Header from "../elements/header/header";
import {graphql} from "gatsby";
import BlogListComponent from "../components/bloglist/bloglist";
import SEO from "../elements/seo";


const StepsPage = ({
                       data: {
                           allMarkdownRemark: {edges},
                       },
                   }) => {

    return <Layout>
        <SEO title="Tipps, Wissen und aktuelle Projekte"
             description={"Der Videobakers Blog hält dich mit aktuellem Wissen in den Bereichen Video, Marketing und Social Media immer auf dem Laufenden. Werden Sie mit uns erfolgreicher! Jetzt lesen"}/>
        <Header title={edges[0].node.frontmatter.title}
                subtitle={edges[0].node.frontmatter.subtitle}
                button={{href: edges[0].node.frontmatter.path, text: 'Jetzt lesen'}}>
            <BlogListComponent entries={edges.slice(1)}/>
        </Header>
    </Layout>;
};

export default StepsPage


export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          html
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            subtitle
            path
            title
            projekt
            author
          }
        }
      }
    }
  }
`;
